<template>
    <div class="v-search">
        <!-- 搜索 -->
        <div class="m-archive-search m-exam-search" slot="search-before" key="exam-search">
            <el-input placeholder="请输入搜索内容" v-model="search" class="input-with-select">
                <span slot="prepend">关键词</span>
                <el-button slot="append" icon="el-icon-search"></el-button>
            </el-input>
        </div>

        <!-- 切换 -->
        <el-tabs class="m-exam-tabs" v-model="type">
            <el-tab-pane v-for="item in types" :label="item.label" :key="item.value" :name="item.value"></el-tab-pane>
        </el-tabs>

        <!-- tags搜索 -->
        <div class="m-exam-tags">
            <span @click="selected(item, i)" class="el-tag el-tag--small" v-for="(item, i) in tags" :key="i" :class="tag_index == i ? 'active' : ''">{{ item }}</span>
        </div>
    </div>
</template>

<script>
import tags from "@/assets/data/exam_tags.json";
export default {
    name: "Search",
    components: {},
    data: function() {
        return {
            // 搜索
            search: "",

            // 类型
            type: "paper",
            types: [
                { label: "综合试卷", value: "paper" },
                { label: "趣味问题", value: "question" },
            ],

            // 标签
            tags: tags,
            tag: "",
            tag_index: 0,
        };
    },
    computed: {},
    watch: {
        search: {
            handler: function(val) {
                this.$emit("update", { key: "search", val: val });
            },
        },
        type: {
            handler: function(val) {
                this.$emit("update", { key: "type", val: val });
            },
        },
        tag: {
            handler: function(val) {
                this.$emit("update", { key: "tag", val: val });
            },
        },
    },
    methods: {
        selected(val, i) {
            this.tag = val;
            this.tag_index = i;
        },
    },
};
</script>

<style lang="less">
@import "~@/assets/css/exam/exam.less";
</style>
